import React, { useState, useEffect, useMemo } from "react";
import Img from "gatsby-image";
import fluidImgProps from "../common/fluidImgProps";

export default ({ imgs }) => {
  const [active, setActive] = useState(0);

  const cycleImages = () => (
    setActive(active === imgs.length - 1 ? 0 : active + 1)
  );

  useEffect(() => {
    setTimeout(cycleImages, 1000);
  });

  return (
    <div className="row article-media">
      <div className="article-imageSequence aspect__3-by-2">
        { imgs.map((img, i) => {
          const props = useMemo(() => fluidImgProps(img));

          return(
            <Img 
              key={"imgSeq-img-"+i}
              className={`article__image article-imageSequence-image ${i === active ? " -active" : ""}`}
              style={{ position: "absolute" }}
              {...props}
            />
          )
        }) }
      </div>
    </div> 
  )
};
